@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;600;700;900&display=swap);
:root {
  --color-primary: #fff;
  --color-secondary: #272341;
  --color-tertiary: #00cc91;
  --color-quaternary: #00d8cf;
  --background-image-gradient: linear-gradient(135deg, var(--color-tertiary), var(--color-quaternary));
  --background-image-gradient-grey: linear-gradient(135deg, #606060 , #acacac);
  --color-gradient: transparent;
  --clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%);
  --clip-path-2: polygon(0 0, 100% 15vh, 100% 100%, 0 100%);
  --clip-path-3: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue",
    Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
  background-color: var(--color-primary);
  color: #272341;
  color: var(--color-secondary);
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-weight: 400;
  font-style: normal;
  font-family: "Circular-Loom";

  src: url("https://cdn.loom.com/assets/fonts/circular/CircularXXWeb-Book-cd7d2bcec649b1243839a15d5eb8f0a3.woff2")
    format("woff2");
}

@font-face {
  font-weight: 500;
  font-style: normal;
  font-family: "Circular-Loom";

  src: url("https://cdn.loom.com/assets/fonts/circular/CircularXXWeb-Medium-d74eac43c78bd5852478998ce63dceb3.woff2")
    format("woff2");
}

@font-face {
  font-weight: 700;
  font-style: normal;
  font-family: "Circular-Loom";

  src: url("https://cdn.loom.com/assets/fonts/circular/CircularXXWeb-Bold-83b8ceaf77f49c7cffa44107561909e4.woff2")
    format("woff2");
}

@font-face {
  font-weight: 900;
  font-style: normal;
  font-family: "Circular-Loom";

  src: url("https://cdn.loom.com/assets/fonts/circular/CircularXXWeb-Black-bf067ecb8aa777ceb6df7d72226febca.woff2")
    format("woff2");
}

a,
a:active,
a:hover,
a:link,
a:visited {
  color: inherit;
  text-decoration: none;
}

h1,
h2,
p {
  -webkit-margin-before: 0em;
          margin-block-start: 0em;
  -webkit-margin-after: 0em;
          margin-block-end: 0em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
}

.card {
  width: 100%;
  margin: 2em 1em;
  padding: 1em;
  z-index: 1;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
}

.card-txt {
  width: 90%;
  max-width: 380px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  margin: 0 auto;
  padding: auto;
  font-size: 17px;
}

.card-title {
  font-size: 1.5em;
  font-weight: bold;
  margin-bottom: 0.8em;
}

.card-description {
  margin-bottom: 0.8em;
}

.card-technologies {
  font-size: 14px;
  color: var(--color-primary);
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.tech {
  background: var(--background-image-gradient);
  margin: 0.4em 0.4em 0 0;
  padding: 0.2em 0.5em;
}

.card-links {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0.8em 0;
}

.deploy-link {
  display: flex;
  align-items: center;
  margin: auto;
  background: var(--color-primary);
  position: relative;
  box-sizing: border-box;
  background-clip: padding-box;
  box-sizing: border-box;
  border: 4px solid var(--color-gradient);
  padding: 0.2rem;
  margin: 0.8rem 0;
  width: 6em;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 1em;
}

.deploy-link:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  margin: -4px;
  border-radius: inherit;
  background: var(--background-image-gradient);
}

.deploy-link:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  margin: -4px;
  border-radius: inherit;
  background: var(--color-gradient);
}

.deploy-link:hover {
  background: var(--background-image-gradient);
}

.no-link:before,
.no-link:hover {
  background: var(--background-image-gradient-grey);
}

.deploy-link-deploy,
.deploy-link-github {
  -webkit-text-fill-color: var(--color-gradient);
  -webkit-background-clip: text;
  background-image: var(--background-image-gradient);
  color: var(--color-gradient);
  border-image-slice: 1;
  font-weight: 700;
}

.no-link-deploy {
  background-image: var(--background-image-gradient-grey);
}

.deploy-link:hover .deploy-link-deploy {
  -webkit-text-fill-color: var(--color-primary);
}

.deploy-link:hover .deploy-link-deploy:after {
  background-image: var(--background-image-gradient);
}

.deploy-link-github:hover {
  transform: translateX(0.1em);
}

.tech:hover,
.card-title:hover {
  transform: translateX(0.05em);
}

.img-div-card{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  padding: auto;
}

.img-card {
  width: 90%;
  max-width: 800px;
  height: auto;
}

.products {
  position: relative;
  width: 100%;
  max-width: 1700px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 4em 0;
}

.products-title {
  font-size: 2.2rem;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-self: center;
}

.see-more-div {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: .5em 0;
}

.see-more {
  display: flex;
  align-items: center;
  margin: auto;
  background: var(--color-primary);
  position: relative;
  box-sizing: border-box;
  background-clip: padding-box;
  border: 4px solid var(--color-gradient);
  padding: 0.2rem;
  margin-top: 1.2rem;
  margin-left: 0;
  width: 8em;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.3em;
}

.see-more:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  margin: -4px;
  border-radius: inherit;
  background: var(--background-image-gradient);
}

.see-more:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  margin: -4px;
  border-radius: inherit;
  background: var(--color-gradient);
}

.see-more:hover {
  background: var(--background-image-gradient);
}

.see-more-txt {
  -webkit-text-fill-color: var(--color-gradient);
  -webkit-background-clip: text;
  background-image: var(--background-image-gradient);
  color: var(--color-gradient);
  border-image-slice: 1;
  font-weight: 700;
}

.see-more:hover .see-more-txt {
  -webkit-text-fill-color: var(--color-primary);
}

.see-more:hover .see-more-txt:after {
  background-image: var(--background-image-gradient);
}
.introduction {
  align-items: center;
  display: flex;
  font-weight: 400;
  height: 100vh;
  min-height: 100vh;
  width: 85%;
  margin-left: 15%;
  max-width: 1700px;
}

.introduction-title {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 3.2rem;
  text-align: left;
}

.introduction-title h1 {
  margin: 0;
}

.introduction-title-name {
  -webkit-text-fill-color: var(--color-gradient);
  -webkit-background-clip: text;
  background-image: var(--background-image-gradient);
  color: var(--color-gradient);
}

.div-btn {
  align-items: center;
  margin: auto;
  background: var(--color-primary);
  position: relative;
  box-sizing: border-box;
  background-clip: padding-box;
  box-sizing: border-box;
  border: 4px solid var(--color-gradient);
  padding: 0.2rem;
  margin: 0.8rem 0;
  width: 7em;
  display: flex;
  align-items: center;
  justify-content: center;
}

.div-btn:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  margin: -4px;
  border-radius: inherit;
  background: var(--background-image-gradient);
}

.div-btn:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  margin: -4px;
  border-radius: inherit;
  background: var(--color-gradient);
}

.div-btn:hover {
  background: var(--background-image-gradient);
}

.btn-intro {
  -webkit-text-fill-color: var(--color-gradient);
  -webkit-background-clip: text;
  background-image: var(--background-image-gradient);
  color: var(--color-gradient);
  border-image-slice: 1;
  font-weight: 700;
}

.div-btn:hover .btn-intro {
  -webkit-text-fill-color: var(--color-primary);
}

.div-btn:hover .btn-intro:after {
  background-image: var(--background-image-gradient);
}

@media(max-width: 800px) {
  .introduction-title {
    font-size: 1.5rem;
    padding-right: 3%;
  }

  .div-btn{
    font-size: 1.2rem;
  }
}

@media(max-width: 600px) {
  .introduction-title {
    font-size: 1.5rem;
    padding-right: 3%;
  }

  .div-btn{
    font-size: 1.3rem;
    width: 7em;
  }
}
.about {
  background-image: var(--background-image-gradient);
  color: var(--color-primary);
  -webkit-clip-path: var(--clip-path);
          clip-path: var(--clip-path);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 3rem 0 7rem 0;
}

@media (max-width: 768px) {
  .about {
    -webkit-clip-path: var(--clip-path-3);
            clip-path: var(--clip-path-3);
    padding: 3rem 0 3rem 0;
  }
}

.about-title {
  font-size: 2.2rem;
  margin-bottom: 1rem;
}

.about-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.about-image {
  width: 100%;
  max-width: 300px;
  height: auto;
  margin: 1.8em;
}

.about-image img {
  width: 100%;
  height: auto;
}

.about-card {
  max-width: 30em;
  margin: 1.8em 3em;
}

.about-txt {
  font-size: 1.1em;
  margin-bottom: 0.6em;
  margin-left: 0;
}

.about-btn-a {
  display: flex;
  align-items: center;
  margin: auto;
  background: var(--background-image-gradient);
  position: relative;
  box-sizing: border-box;
  background-clip: padding-box;
  border: 4px solid var(--color-gradient);
  padding: 0.2rem;
  margin-top: 1.2rem;
  margin-left: 0;
  width: 8em;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.3em;
}

.about-btn-a:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  margin: -4px;
  border-radius: inherit;
  background: var(--color-primary);
}

.about-btn-a:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  margin: -4px;
  border-radius: inherit;
  background: var(--color-gradient);
}

.about-btn-a:hover {
  background: var(--color-primary);
}

.btn-about {
  -webkit-text-fill-color: var(--color-primary);
  -webkit-background-clip: text;
  background-image: var(--color-primary);
  color: var(--color-gradient);
  border-image-slice: 1;
  font-weight: 700;
}

.about-btn-a:hover .btn-about {
  -webkit-text-fill-color: var(--background-image-gradient);
