.contact {
  background-image: var(--background-image-gradient);
  color: var(--color-primary);
  -webkit-clip-path: var(--clip-path-2);
          clip-path: var(--clip-path-2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 7rem 0 3rem 0;
}

@media (max-width: 768px) {
  .contact {
    -webkit-clip-path: var(--clip-path-3);
            clip-path: var(--clip-path-3);
    padding: 3em 0 3rem 0;
  }  
}

.contact-title {
  font-size: 2.2rem;
  margin-bottom: 1.4rem;
}

.contact-line {
  border: 1px solid var(--color-primary);
  width: 70%;
  max-width: 500px;
}

.contact-info {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.contact-info-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1rem;
  margin-bottom: 0;
  font-size: 19px;
}

.contact-info-item-icon {
  font-size: 1.8rem;
  margin-top: 1rem;
}

.footer {
  background-color: #333;
  color: #fff;
  width: 100%;
  background-clip: border-box;
}

.footer-container {
  width: 100%;
  max-width: 700px;
  margin: 3em auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

.footer-container hr {
  width: 90%;
  border: 0;
  border-top: 2px solid rgb(94, 93, 93);
}

.footer-home {
  font-size: 35px;
  margin-bottom: 1em;
}

.footer-home:hover {
  transform: translateY(-2px);
}

.footer-link-a {
  font-size: 35px;
  margin: 1.5em;
}

.footer-link-a:hover {
  transform: translateY(2px);
}

.footer-copy {
  color: rgb(94, 93, 93);
  font-size: 0.8em;
  margin-top: 1em;
}

.footer-copy a:hover {
  color: var(--color-tertiary);
}

.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

